import { combineReducers } from 'redux';

console.log(42);

const gameHighestScoreReducer = (state = null, action) => {
    switch (action.type) {
        case 'UPDATE_GAME_HIGHEST_SCORE': {
            return action.payload;
        }
        default:
            return state;
    }
};

const playerDataReducer = (
    state = { screenName: '', facebookId: '', highestScore: 0 },
    action
) => {
    switch (action.type) {
        case 'NEW_PLAYER_REGISTRATION': {
            return action.payload;
        }
        case 'UPDATE_FACEBOOKID': {
            console.log(41);
            state.facebookId = action.payload;
            return state;
        }
        default:
            return state;
    }
};

const allReducers = combineReducers({
    gameHighestScore: gameHighestScoreReducer,
    playerData: playerDataReducer,
});

export default allReducers;



